<template>
  <div class="hot">
    <!--轮播图-->
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide"><img src="../../imgs/rowing/s1.png" width="100%"></div>
        <div class="swiper-slide"><img src="../../imgs/rowing/s2.png" width="100%"></div>
        <div class="swiper-slide"><img src="../../imgs/rowing/s3.png" width="100%"></div>
        <div class="swiper-slide"><img src="../../imgs/rowing/s4.png" width="100%"></div>
        <div class="swiper-slide"><img src="../../imgs/rowing/s5.png" width="100%"></div>
        <div class="swiper-slide"><img src="../../imgs/rowing/s6.png" width="100%"></div>
        <div class="swiper-slide"><img src="../../imgs/rowing/s7.png" width="100%"></div>
        <div class="swiper-slide"><img src="../../imgs/rowing/s8.png" width="100%"></div>
        <div class="swiper-slide"><img src="../../imgs/rowing/s9.png" width="100%"></div>
      </div>
      <!--分页-->
      <div class="swiper-pagination"></div>
    </div>
    <!--中间导航-->
    <hot-nav></hot-nav>
    <!--广告位-->
    <div class="hot-ad">
      <img src="../../imgs/hot_ad/home_ad.gif" width="100%">
      <hot-shop-list></hot-shop-list>
    </div>
  </div>
</template>

<script>
  import Swiper from 'swiper'
  import 'swiper/dist/css/swiper.css'

  import HotNav from './HotNav'
  import HotShopList from  './HotShopList'
  export default {
    name: "Hot",
    components:{
      HotNav,
      HotShopList
    },
    mounted() {
      //创建swiper实例
      new Swiper ('.swiper-container', {
        autoplay:true,//等同于以下设置
        loop: true, // 循环模式选项

        // 分页器
        pagination: {
          el: '.swiper-pagination',
        }
      })
    }
  }
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
  .hot
    width 100%
    height 100%
    margin-top 46px
    .hot-ad
        background-color #fff
        margin 8px 0
        padding 5px

</style>



