<template>
  <div class="home">
    <ly-tab class="flx" v-model="selectedId" :items="items" :options="options" @change="handleChange">
    </ly-tab>
    <router-view></router-view>
  </div>
</template>

<script>

export default {
  name: 'Home',
  data(){
    return{
      selectedId : 0,
      items:[
        {label:'热门'},
        {label:'服饰'},
        {label:'鞋包'},
        {label:'母婴'},
        {label:'百货'},
        {label:'食品'},
        {label:'内衣'},
        {label:'男装'},
        {label:'电器'}
      ],
      options:{
        activeColor:'#e9232c', //设置选中的颜色
      },
      //二级路由路劲
      subRouteUrl:[
        '/home/hot','/home/dress','/home/box','/home/mbaby','/home/general','/home/food','/home/shirt','/home/man','/home/ele'
      ]
    }
  },
  methods: {
    handleChange(item,index){
      this.$router.replace(this.subRouteUrl[index])
    }
  }
}
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
.home
  height 100%
  .flx
    position fixed
    top 0
    left 0
    z-index 998
</style>



