import Vue from 'vue'
import App from './App'
import store from './store/index'

// 引入路由器
import router from './router/index'
import Lytab from 'ly-tab'

Vue.use(Lytab)
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
})



