import {
  getHomeCausal,
} from '../api/index'

import {
  HOME_CASUAL
} from './mutation-types'

export default {
  //1.获取首页轮播图
  async reqHomeCausal({commit}){
      const result = await getHomeCausal();
      commit(HOME_CASUAL,{homecausal:result.message.data})
  }
}




